<template>
  <div class="bg-in">
    <loading :active.sync="isLoading">
      <img alt="Meason" src="../../assets/img/loading.svg" width="100">
    </loading>
    <div class="modal-dialog modal-dialog-centered animated zoomIn" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h3 class="modal-title h5">我想加入</h3>
          <a @click.prevent="$router.go(-1)" href="#">
            <span aria-hidden="true">&times;</span>
          </a>
        </div>
        <div class="modal-body">
          <form>
            <div class="form-group mb-0">
              <div class="d-flex align-items-center">
                <h4 class="h6 mr-3">音樂作品: </h4>
                <div class="radio mr-3">
                  <input type="radio" name="resume-radio"
                    v-model="tempDom.select" id="resume-1" value="item1">
                  <label for="resume-1">
                    個人空間作品
                  </label>
                </div>
                <div class="radio">
                  <input type="radio" name="resume-radio"
                    v-model="tempDom.select" id="resume-2" value="item2">
                  <label for="resume-2">
                    外部連結
                  </label>
                </div>
              </div>
              <div class="input-group animated flipInX"
                v-if="tempDom.select === 'item2'">
                <div class="input-group-prepend">
                  <span class="input-group-text" id="music-link"><i class="fas fa-link"></i></span>
                </div>
                <input type="text" class="form-control"
                  placeholder="請提供作品集網址，如: http://example.com" aria-describedby="music-link"
                  name="urls" :class="{'is-invalid': errors.has('urls')}"
                  v-model="formData.url" v-validate="{url: {require_protocol: true }}" data-vv-as="作品集網址">
              </div>
              <p class="text-danger m-0" v-if="errors.has('urls')">
                {{ errors.first('urls') }}
              </p>
              <textarea class="form-control mt-3" id="message-text" rows="6"
                name="msg"
                :class="{'is-invalid': errors.has('msg')}"
                v-validate="'required|max:200'"
                data-vv-as="訊息"
                placeholder="說點甚麼吧 ヽ(´･ω･`)ﾉ ~♫" v-model="formData.msg"></textarea>
              <p class="text-danger m-0" v-if="errors.has('msg')">
                {{ errors.first('msg') }}
              </p>
            </div>
          </form>
        </div>
        <div class="modal-footer">
          <button @click="$router.go(-1)" type="button" class="btn btn-outline-secondary" >
            取消
          </button>
          <button type="button" class="btn btn-primary" @click="sendMail()">送出</button>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.bg-in{
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,.5);
  z-index: 1001;
  .modal-dialog{
    .modal-title{
      font-weight: bold;
    }
    .modal-body{
      .modal-con{
        max-height: calc(100vh - 222px);
        overflow: auto;
        &::-webkit-scrollbar {
          width: 5px;
          height: 5px;
        }
        &::-webkit-scrollbar-track {
          background: #eee;
        }
        &::-webkit-scrollbar-thumb {
          background-color: #ccc;
        }
        &::-webkit-scrollbar-thumb:hover {
          background-color: #fecd88;
        }
        .pic{
          height: 100%;
          min-height: 200px;
          background-position: center;
          background-size: cover;
        }
        .info-con{
          display: flex;
          flex-direction: column;
          justify-content: center;
          ul{
            list-style: none;
            padding: 0;
            margin-bottom: 5px;
            li{
              border-top: 1px dashed #ccc;
              padding: 5px 10px;
              display: flex;
              align-items: flex-end;
              flex-wrap: wrap;
              p{
                margin-bottom: 0;
              }
              &:first-child{
                border: none;
              }
              .need:before{
                content: '、';
              }
              .need:first-child:before{
                content: none;
              }
              b{
                margin-right: 8px;
                font-weight: normal;
                color: #999;
                font-weight: bold;
                font-size: 14px;
                white-space: nowrap;
                svg{
                  color: #fe9a3b;
                }
              }
            }
          }
          .contact{
            border-left: 8px #ffe587 solid;
            background-color: #f3f3f3;
            padding: 5px 10px;
            margin-bottom: 0;
            span{
              display: block;
              font-weight: bold;
              font-size: 14px;
              color: #999;
            }
          }
        }
        .bio{
          b{color: #999; font-size: 14px;}
          .pre{
            border: 1px solid #ccc;
            min-height: 150px;
            padding: 5px 10px;
          }
        }
      }
    }
    .modal-footer{
      justify-content: center;
      .btn{
        letter-spacing: 3px;
        svg{
          transition: all 1s ease-in;
          margin-right: 10px;
          transform: rotate(0deg) scale(1.3);
        }
        &:hover{
          svg{
            transform: rotate(1440deg) scale(1.2);
          }
        }
      }
    }
  }
}
</style>

<script>
import $ from 'jquery';

export default {
  props:['userData', 'poster_email', 'postType'],
  data() {
    return {
      isLoading: false,
      data: '',
      tempDom:{
        select: 'item1',
      },
      formData:{
        url: '',
        msg: '',
      }
    }
  },
  methods: {
    sendMail() {
      const vm = this;
      this.$validator.validate().then((result) => {
        if (result) {         
          vm.sendAPI();
        } else {
          console.log('欄位不完整')
        }
      });      
    },
    sendAPI() {
      const vm = this;
      let str = '';
      vm.isLoading = true;
      const url = `${process.env.VUE_APP_API}/sendResumeMail`;
      str = `
      <h1>嗨~</h1>
      ${vm.formData.msg}
      `
      this.$http.post(url, {
        "str": str,
        "userData": vm.userData,
        "toMail": vm.poster_email,
        "type": vm.postType,
      }).then((response) => {
        vm.$router.go(-1);
        vm.isLoading = false;
      })
      .catch(function(error){
        console.error("寫入使用者資訊錯誤",error);
      });
    }
  },
  created() {
  },
}
</script>
